import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

Vue.use(Vuex)

export const store = new Vuex.Store({
    state: {
        facts: null,
        errored:false,
        error:{},
        loading: true,
    },
    getters: {
        FACTS: state => {
            return state.facts
        },

    },
    mutations: {
        SET_FACTS(state, payload) {
            state.facts = payload
        },
        ADD_FACT(state, payload) {
            state.facts.push(payload)
        },
        CHANGE_LOADING_STATE(state, payload) {
            state.loading = payload
          },
        CHANGE_ERRORED_STATE(state, payload) {
            state.errored = payload
          },
         SAVE_ERROR: (state, {error}) => {
            state.error = error
        }  
    },
    actions: {
        getFacts({ commit }) {
            axios
                .get("https://catfact.ninja/facts?limit=1000")
                .then(res => {
                    commit('SET_FACTS', res.data.data);
                    commit('CHANGE_LOADING_STATE', false);
                })
                .catch(error => {
                    console.log(error);
                    commit('CHANGE_ERRORED_STATE', true);
                    commit('SAVE_ERROR', {error: error});
                    commit('CHANGE_LOADING_STATE', false);
                })
        },
    },
  
})
